<template>
    <div class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
        <h1 class="h2">Laporan Pendapatan</h1>
    </div>
    <div class="container mt-5">
        <div class="row">
            <div class="col-md-12">
                <button  class="btn btn-md-3 btn-success"  @click="downloadPDF()">Cetak</button>
                <div class="card border-0 rounded shadow" ref='content'>
                    <div class="card-body" > 
                        <div>
                            <h2>Periode : {{ tahun }}</h2>
                            <h2>Tanggal Cetak  : {{ tanggal_cetak }}</h2>
                        </div>
                        <table class="table table-striped table-bordered mt-4" border="2">                           
                            <thead class="thead-dark">
                                <tr>
                                    <th scope="col">BULAN</th>
                                    <th scope="col">DEPOSIT UANG</th>          
                                    <th scope="col">DEPOSIT KELAS</th> 
                                    <th scope="col">AKTIVASI</th>   
                                    <th scope="col">TOTAL</th>                                                     
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(lapPendapatan, id) in lapPendapatans" :key="id">
                                    <td>{{ lapPendapatan.bulan}}</td>
                                    <td align="right" >{{ lapPendapatan.deposit_uang}}</td>
                                    <td align="right">{{ lapPendapatan.deposit_kelas}}</td>
                                    <td align="right">{{ lapPendapatan.aktivasi}}</td>
                                    <td align="right">{{ lapPendapatan.total}}</td>
                                </tr>
                                <tr>                                   
                                    <td colspan="4" align="right"><B>Total</B></td>
                                    <td align="right">{{ total }}</td>
                                </tr>
                            </tbody> 
                        </table>                        
                    </div> 
                    <div class="card-body">
                    <div class="d-flex justify-content-between flex-wrap flex-mdnowrap align-items-center pt-3 pb-2 mb-3">
                        <h3><b><u>Graph Laporan Bulanan</u></b></h3>
                        <canvas id="myChart"></canvas>
                    </div>
                </div>
                </div> 
            </div> 
        </div> 
    </div> 
</template>
<script> 
import axios from 'axios' 
import { onMounted, ref } from 'vue' 
import Chart from 'chart.js/auto'
// import jsPDF from 'jspdf'

export default { 
    setup() { 
        //reactive state 
        let lapPendapatans = ref([]) 
        let total = ref([]) 
        let tahun = ref([]) 
        let tanggal_cetak = ref([]) 

        

        //mounted 
        onMounted(() => {
            //get API from Laravel Backend 
            axios.get('https://api-gofit.200710947.com/api/laporan/pendapatan') 
            .then(response => { 
                //assign state posts with response data 
                lapPendapatans.value = response.data.data 
                total.value = response.data.total_semua
                tahun.value = response.data.tahun 
                tanggal_cetak.value = response.data.tanggal_cetak

                const ctx = document.getElementById('myChart');

                new Chart(ctx, {
                    type: 'bar',
                    data: {
                    labels: lapPendapatans.value.map(item => item.bulan),
                    datasets: [{
                        label: '# Total Perbulannya',
                        data: lapPendapatans.value.map(item => item.total),
                        borderWidth: 1
                    }]
                    },
                    options: {
                    scales: {
                        y: {
                            beginAtZero: true
                        }
                    }
                    }
                });
            }).catch(error => {
                 console.log(error.response.data) 
            }) 

        }) 

        return { lapPendapatans, total, tahun, tanggal_cetak} 
    },
    

        
        //     downloadPDF(){

        //     var html = this.$refs.content
            
        //     let newWin = '';

        //     newWin = window.open("");
        //     newWin.document.write(html.outerHTML);
        //     newWin.print();
        //     newWin.close();

        // }
        // }
methods:{
        downloadPDF(){
            var html = this.$refs.content;

            let newWin = '';
            newWin = window.open("");
            newWin.document.write(html.outerHTML);
            newWin.print();
            newWin.close();
        }
    } 
}
    </script> 

<style>
</style>
